.intro{
    display: flex;
    height: 77vh;
    margin-top: 6em;
}

.i-left{
    display: flex;
    flex: 1;
    position: relative;
    flex-direction: column;
    gap: 2rem;
}

.i-name{
    display: flex;
    flex-direction: column;
}

.i-name>:nth-child(1){
    color: var(--black);
    font-weight: bold;
    font-size: 2rem;
}

.i-name>:nth-child(2){
    color: var(--orange);
    font-weight: bold;
    font-size: 2rem;
}

.i-name>:nth-child(3){
    font-weight: bold;
    font-size: 14px;
    color: var(--gray);
    margin-top: 10px;
}

.i-button{
    width: 4rem;
    height: 2rem;
    margin-top: -1rem;
}

.i-icons{
    margin-top: 1rem;
    display: flex;
    gap: 0rem;
}

.i-icons>*{
    transform: scale(.5);
}

.i-icons>*:hover{
    cursor: pointer;
}



.i-right{
    flex: 1;
    position: relative;

}

.i-right>*{
    position: absolute;
    z-index: 1;
}

.i-right>:nth-child(1){
    top: -4.6rem;
    left: 1rem;
    transform: scale(.80);
}

.i-right>:nth-child(2){
    transform: scale(.4);
    top: -12rem;
    
}

.i-right>:nth-child(3){
    transform: scale(.6);
    border-radius: 50%;
} 

.crown{
    top: 20rem;
}

@media screen and (max-width:480px){

    .intro{
        flex-direction: column;
        height: 45rem;
        gap: 5rem;
    }

    .i-right{
        transform: scale(0.7);
        left: -5.6rem;
        top: -3rem;
    }

    .floating-div:nth-of-type(1){
        top: -4rem !important;
    }


    .floating-div:nth-of-type(2){
        top: 10rem !important;
        left: 5rem !important;
    }

    
}

