
.n-wrapper{
    height: 10vh;
    display: flex;
    justify-content: space-between;
     /* position: fixed;
     width: 100%;
     z-index: 1; */ 
    
}
.n-left{
    flex: 1;
    align-items: center;
    display: flex;
    gap: 2rem;
}

.n-name{
    font-size: 1.3rem;
    font-weight: bold;
}

.n-right{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}

.n-list{
    flex: 10;
}

.n-list>ul{
    display: flex;
    gap: 2rem;
    margin-right: 1rem;
    cursor: pointer;

}

.n-list>ul>li:hover{
    cursor: pointer;
    color: var(--orange);
}

.n-button{
    flex: 2;
    width: 5rem;
    height: 2rem;

}

#Link{
    font-size: large

}

@media screen and (max-width:480px) {
    .n-list{
        display: none;
    }

    .n-button{
        display: none;
    }
}