:root {
  --yellow: #F5C32C;
  --orange: #FCA61F;
  --black: #242D49;
  --gray: #788097;
  --blueCard: #DDF8FE;
  --purple: rgb(238 210 255);
  --boxShadow: 0px 19px 60px rgb(0 0 0 / 8%);
  --orangeCard: rgba(252, 166, 31, 0.45);
  --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App {
  padding: 0.5rem 3.5rem;
  overflow: hidden;
  color: #242D49;
  color: var(--black);
}

.button {
  border-radius: 34px;
  border: 1px solid rgb(189, 184, 184);
  color: rgb(88, 63, 63);
  font-size: 16px;
  padding: 11px, 26px,11px,26px;
  background: linear-gradient(180deg, #F6D285, #BBF0F3);
  box-shadow: 0px 20px 24px 3px rgba(177, 231, 245, 0.45);

}

.button:hover{
  background: black;
  color: #FCA61F;
  color: var(--orange);
  cursor: pointer;
  border: 1px solid #FCA61F;
  border: 1px solid var(--orange);
}

@media screen and (max-width:480px) {
  .App{
    padding: 0.5rem 1rem;
  }
}


.n-wrapper{
    height: 10vh;
    display: flex;
    justify-content: space-between;
     /* position: fixed;
     width: 100%;
     z-index: 1; */ 
    
}
.n-left{
    flex: 1 1;
    align-items: center;
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
}

.n-name{
    font-size: 1.3rem;
    font-weight: bold;
}

.n-right{
    display: flex;
    flex: 1 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}

.n-list{
    flex: 10 1;
}

.n-list>ul{
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    margin-right: 1rem;
    cursor: pointer;

}

.n-list>ul>li:hover{
    cursor: pointer;
    color: var(--orange);
}

.n-button{
    flex: 2 1;
    width: 5rem;
    height: 2rem;

}

#Link{
    font-size: large

}

@media screen and (max-width:480px) {
    .n-list{
        display: none;
    }

    .n-button{
        display: none;
    }
}

.toogle{
    display: flex;
    justify-content: space-between;
    border: 3px solid var(--orange);
    border-radius: 1rem;
    position: relative;
    padding: 2px;
    cursor: pointer;
}
.toogle>*{
    width: 1rem;
    height: 1rem;
    color: var(--orange);
}

.t-button{
    border-radius: 100%;
    background: var(--orange);
    position: absolute;
}
.intro{
    display: flex;
    height: 77vh;
    margin-top: 6em;
}

.i-left{
    display: flex;
    flex: 1 1;
    position: relative;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
}

.i-name{
    display: flex;
    flex-direction: column;
}

.i-name>:nth-child(1){
    color: var(--black);
    font-weight: bold;
    font-size: 2rem;
}

.i-name>:nth-child(2){
    color: var(--orange);
    font-weight: bold;
    font-size: 2rem;
}

.i-name>:nth-child(3){
    font-weight: bold;
    font-size: 14px;
    color: var(--gray);
    margin-top: 10px;
}

.i-button{
    width: 4rem;
    height: 2rem;
    margin-top: -1rem;
}

.i-icons{
    margin-top: 1rem;
    display: flex;
    grid-gap: 0rem;
    gap: 0rem;
}

.i-icons>*{
    -webkit-transform: scale(.5);
            transform: scale(.5);
}

.i-icons>*:hover{
    cursor: pointer;
}



.i-right{
    flex: 1 1;
    position: relative;

}

.i-right>*{
    position: absolute;
    z-index: 1;
}

.i-right>:nth-child(1){
    top: -4.6rem;
    left: 1rem;
    -webkit-transform: scale(.80);
            transform: scale(.80);
}

.i-right>:nth-child(2){
    -webkit-transform: scale(.4);
            transform: scale(.4);
    top: -12rem;
    
}

.i-right>:nth-child(3){
    -webkit-transform: scale(.6);
            transform: scale(.6);
    border-radius: 50%;
} 

.crown{
    top: 20rem;
}

@media screen and (max-width:480px){

    .intro{
        flex-direction: column;
        height: 45rem;
        grid-gap: 5rem;
        gap: 5rem;
    }

    .i-right{
        -webkit-transform: scale(0.7);
                transform: scale(0.7);
        left: -5.6rem;
        top: -3rem;
    }

    .floating-div:nth-of-type(1){
        top: -4rem !important;
    }


    .floating-div:nth-of-type(2){
        top: 10rem !important;
        left: 5rem !important;
    }

    
}


.FloatingDiv{
    justify-content: space-around;
    display: flex;
    background: white;
    box-shadow: var(--boxShadow);
    border-radius: 17px;
    align-items: center;
    padding: 0px, 28px, 0px, 0px;
    height: 4rem;
    padding-right: 13px;

}

.FloatingDiv>img{
    -webkit-transform: scale(0.4);
            transform: scale(0.4);
}

.FloatingDiv>span{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 19px;
    color: black;
}

.services{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-bottom: 5rem;
    margin-top: 7rem;

}

.awesome{
    display: flex;
    flex-direction: column;
    position: relative;
}

.awesome>:nth-child(1){
    color: var(--black);
    font-size: 2.5rem;
    font-weight: bold;
}

.awesome>:nth-child(2){
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}

.awesome>:nth-child(3){
    color: var(--gray);
    font-size: 14px;
    margin-top: 1rem;
    font-weight: bold;
}

.s-button{
    width: 8rem;
    height: 2rem;
    margin-top: 1rem;
}

.cards{
    position: relative;
}
.cards>*{
    position: absolute;
}


@media screen and (max-width:480px){
    .services{
        margin-top: 0;
        flex-direction: column;
        grid-gap: 5rem;
        gap: 5rem;
        height: 60rem;
        padding: 0;
    }

    .cards{
        display: flex;
        flex-direction: column ;
        grid-gap: 13rem;
        gap: 13rem;
    }

    .cards>*{
        position: static;
    }
}

.card{
    width: 4rem;
    height: 9rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: center;
    text-align: center;
    width: 10rem;
    background: rgba(255, 255, 255, .26);
    border: 7px solid var(--orangeCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    padding: 0px 26px 2rem 26px;

}

.card :nth-child(3){
    color: var(--gray);
    font-size: 16px;
}

.card>img{
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    margin-bottom: -2rem;
    margin-top: -1rem;
}

.c-button{
    background: #FFFFFF;
    box-shadow: 0px 19px 60px rgba(0, 0, 0, .08);
    border-radius: 7px;
    border: none;
    padding: 10px;
    font-size: 16px;
    color: rgb(38, 129, 248);
}


.skills{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    padding-top: 4rem;
}

/* right side */
.s-right{
    position: relative;
}

.s-mainCircle{
    left: 7rem;
    position: relative;
    width: 18rem;
    height: 18rem;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    top: 3rem;
    background: white;
    z-index: 2;

}

.s-mainCircle>*{
    position: absolute;
}

.s-mainCircle>:nth-child(1){
    top: -2.5rem;
    left: 7rem;
}

.s-mainCircle>:nth-child(2){
    top: 6.5rem;
    left: -2rem;
}

.s-mainCircle>:nth-child(3){
    top: 6.5rem;
    left: 7rem;
}

.s-mainCircle>:nth-child(4){
    top: 6.5rem;
    left: 15.5rem;
}

.s-mainCircle>:nth-child(5){
    top: 15.5rem;
    left: 7rem;
}


.s-secCircle{
    width: 4rem;
    height: 4rem;
    position: absolute;
    border-radius: 100%;
    border: 5px solid #ebebeb;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    box-shadow: var(--smboxShadow);
    background: white;
}

.s-secCircle>img{
    -webkit-transform: scale(.7);
            transform: scale(.7);
}

.s-backCircle{
    position: absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;
    z-index: 1;
}

.blueCircle{
    background: rgb(6, 6, 146);
    left: 11rem;
    top: -1rem;
    z-index: -1;

}

.yellowCircle{
    background: rgb(236, 215, 25);
    left: 11rem;
    top: 7rem;
    z-index: -4;

}

@media screen and (max-width:480px){
    .skills{
        flex-direction: column;
        height: 50rem;
        padding: 0;
        grid-gap: 5rem;
        gap: 5rem;
        /* top: 15rem; */
        bottom: -3rem;
    }

    .s-right{
        display: flex;
        align-items: flex-start;
        justify-content: start;
        -webkit-transform: scale(0.6);
                transform: scale(0.6);
        left: -1rem;
    }

    .s-mainCircle{
        flex-direction: row;
        top: -3rem;
        left: -2rem;
    }

    .s-secCircle{
        left: 8rem !important;
    }
   .blueCircle{
       display: none;
   }
   .yellowCircle{
       display: none;
   }
   .s-mainCircle{
       flex-direction: row;
   }
  
}
.contact{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 80vh;
