.contact{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 80vh;
    margin-top: 4rem;
      /* scroll */
  padding-top: 3rem;
}



.c-right{
    display: flex;
    justify-content: center;
    position: relative;
    flex: 1;
}

.c-right>form
{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    
}

.c-right .user{
    width: 20rem;
    height: 2rem;
    padding: 0.3em;
    outline: none;
    border: 2px solid var(--orange);
    border-radius: 8px;
    font-size: 16px;   
}

textarea{
    height: 4rem!important;
}


@media screen and (max-width: 480px) {

    .contact{
        margin-top: -13rem;
        flex-direction: column;
        gap: 3rem;
        height: 45rem;
        padding: 0;
        top: -10rem;
    }

    .contact-form{
        padding: 0;
        display: flex;
        flex-direction: column;
        gap: 5rem;
        height: 40rem;
        
    }
    .c-right .user{
        width: 12rem;
    }

}   


#c-button{
    height: 2rem;
    width: 3rem;
}

